.downArrow {
  position: absolute;
  width: 10px;
  height: 10px;
  right: 20px;
  top: 20px;
  transition-duration: 0.5s;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}

.dropdownButtons {
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 35px;
  border: none;
